<template>
  <li
    class="chat-window"
    :class="{ focused: buddy.focused }"
    @click="on_window_click"
  >
    <div ref="chat-container" class="chat-window__container">
      <div class="chat-window__title" @click.prevent="toggle_minimize">
        <div class="avatar-container" v-if="buddy.avatar">
          <img :src="buddy.avatar" class="avatar" :alt="buddy.title" />
          <span
            v-if="buddy.type == 'one2one'"
            class="status"
            :class="{ online: is_user_online(buddy.id) }"
          ></span>
        </div>
        <div class="flex-r">
          <div>
            <div class="chat-buddy anchor ellipsis">
              {{ $decodeEntities(buddy.title) }}
              <transition name="fade"
                ><span
                  v-if="buddy.unread_msg_count > 0"
                  class="badge badge-danger"
                  :class="{ jump: jump }"
                  >{{ buddy.unread_msg_count }}</span
                ></transition
              >
            </div>
            <div class="mute">
              {{
                is_user_online(buddy.id)
                  ? msgOnline
                  : msgOffline
              }}
            </div>
          </div>
        </div>
        <a
          class="chat_window__close-btn"
          href="#"
          @click.prevent.stop="$emit('close', buddy)"
          ><span class="dashicons dashicons-no-alt"></span
        ></a>
      </div>
      <div class="chat-window__message-list" v-bar="{preventParentScroll: true}">
        <div ref="messageList">
          <ul class="bpc-chat-list">
            <li
              v-for="(group, index) in sorted_messages"
              :class="get_message_class(group)"
              :key="index"
            >
              <time v-if="should_display_date(index)">{{
                formated_time(group.fCAt)
              }}</time>
              <div class="message-block">
                <img
                  v-if="group.from_id != me"
                  :src="group.from_avatar"
                  class="avatar"
                  :alt="$decodeEntities(buddy.title)"
                />
                <div class="messages">
                  <div
                    v-for="message in group.messages"
                    class="message"
                    v-html="formated_message(message.message)"
                    :key="message.id"
                  ></div>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div>
        <editable
          v-model="input"
          :id="buddy.id"
          :type="buddy.type"
          :minimized="minimized"
          @submit="handle_message_input"
          :placeholder="msgWriteMessage"
        ></editable>
      </div>
    </div>
  </li>
</template>

<script>
import Editable from './Editable'
import data from 'emoji-mart-vue-fast/data/twitter.json'
import { EmojiIndex } from 'emoji-mart-vue-fast'
import emojiRegex from 'emoji-regex'

var defaultTitle = document.title
let emojiIndex = new EmojiIndex(data)
const COLONS_REGEX = new RegExp(
  '([^:]+)?(:[a-zA-Z0-9-_+]+:(:skin-tone-[1-6]:)?)',
  'g'
);
const EMOJI_REGEX = emojiRegex();
const sentHashes = [];

export default {
  name: "chat-window",
  components: {
    editable: Editable
  },
  props: ["buddy", "users", "online_users"],
  data() {
    return {
      input: "",
      messages: [],
      grouped_messages: [],
      page: 1,
      loading: false,
      me: bpc_data.me,
      msgOnline: bpc_data.messages.online,
      msgOffline: bpc_data.messages.offline,
      msgWriteMessage: bpc_data.messages.write_your_message,
      minimized: false,
      jump: false,
    };
  },
  computed: {
    sorted_messages() {
      return this.grouped_messages.sort((m1, m2) => {
        return m1.fCAt - m2.fCAt;
      });
    },
  },
  methods: {
    toggle_minimize() {
      this.minimized = !this.minimized;
      if (!this.minimized) {
        this.$set(this.buddy, "unread_msg_count", 0);
        this.$refs["chat-container"].style.bottom = `0`;
      } else {
        let cs = window.getComputedStyle(this.$refs["chat-container"]);
        let windowHieght = this.$refs["chat-container"].offsetHeight;
        let titleHeight = this.$refs["chat-container"].querySelector(
          ".chat-window__title"
        ).offsetHeight;
        this.$refs["chat-container"].style.bottom = `${-(
          windowHieght -
          titleHeight -
          parseInt(cs.borderTopWidth) -
          parseInt(cs.paddingTop)
        )}px`;
      }
    },
    on_window_click(event) {
      this.buddy.focused = true;
      this.$emit("on_window_click", this.buddy);

      this.$set(this.buddy, "unread_msg_count", 0);
      if (defaultTitle) document.title = defaultTitle;
    },
    async handle_message_input(text) {
      if (!text.trim()) return;
      let message = {
        client_hash: Math.random().toString(32).substring(2, 7),
        from_id: bpc_data.me,
        message: text.trim(),
        createdAt: parseInt(Date.now() / 1000),
        saving: true,
      };
      if (this.buddy.type == "one2one") {
        message.to_id = this.buddy.id;
      } else {
        message.tog_id = this.buddy.id;
      }
      sentHashes.push({
        client_hash: message.client_hash
      });
      this.store_message(message, true);
      this.$nextTick(() => {
        this.input = "";
      });
      this.scroll_to_bottom();

      let formData = new FormData();
      formData.append("to_id", this.buddy.id);
      formData.append("client_hash", message.client_hash);
      formData.append("message", message.message);
      formData.append("type", this.buddy.type);
      // this.$forceUpdate()
      let res = await fetch(bpc_data.send_message_url, {
        method: "POST",
        body: formData,
      });
      await res.json();
      message.saving = false;
    },
    async load_messages(forceToBottom) {
      if (this.loading || this.ended) return;
      //console.log('loading ...')
      this.loading = true;

      let ul = this.$refs.messageList.childNodes[0];
      let previousListLength = 0;
      let previousMessageLength = 0;

      let res = await fetch(
        bpc_data.get_message_url +
          `&buddy=${this.buddy.id}&page=${this.page}&chat_type=${
            this.buddy.type
          }&skip_ids=${this.skip_ids.join(",")}`
      );
      let json = await res.json();
      this.loading = false;

      if (ul.childNodes.length > 0) {
        previousListLength = ul.childNodes.length;
        previousMessageLength = ul.childNodes[0].querySelectorAll(".message")
          .length;
      }
      this.store_message(json, false);

      if (json.length == 10) {
        this.page += 1;
      } else {
        this.ended = true; // means no more message left to load
      }

      this.$nextTick(() => {
        if (forceToBottom && json.length) this.scroll_to_bottom();
        else if (json.length) {
          if (previousListLength && previousMessageLength) {
            let childNodes = ul.childNodes[
              ul.childNodes.length - previousListLength
            ].querySelector(".messages").childNodes;
            childNodes[
              childNodes.length - previousMessageLength
            ].scrollIntoView({ block: "center", inline: "nearest" });
          } else if (previousListLength)
            ul.childNodes[
              ul.childNodes.length - previousListLength
            ].scrollIntoView({ block: "center", inline: "nearest" });
        }
      });
    },
    waited_load_messages() {
      if (this.load_message_timeout) clearTimeout(this.load_message_timeout);
      this.load_message_timeout = setTimeout(async () => {
        if (this.skip_ids.length < 10) {
          await this.load_messages(true);
          this.skip_ids = [];
        } else {
          this.skip_ids = [];
        }
      }, 3000);
    },
    group_message(message, append = false) {
      message.createdAt = parseInt(message.createdAt);
      let group = this.grouped_messages.find((g) => {
        return (
          g.from_id == message.from_id &&
          g.fCAt < message.createdAt + 10 &&
          g.lCAt > message.createdAt - 10
        );
      });
      if (group) {
        let lastG = this.grouped_messages[this.grouped_messages.length - 1];
        let firstG = this.grouped_messages[0];
        if (append && lastG.from_id != group.from_id) {
          group = {
            from_id: message.from_id,
            from_avatar: message.from_avatar,
            fCAt: message.createdAt,
            lCAt: message.createdAt,
            messages: [message],
          };
          this.grouped_messages.push(group);
        } else if (!append && firstG.from_id != group.from_id) {
          group = {
            from_id: message.from_id,
            from_avatar: message.from_avatar,
            fCAt: message.createdAt,
            lCAt: message.createdAt,
            messages: [message],
          };
          this.grouped_messages.push(group);
        } else if (group.fCAt > message.createdAt) {
          group.messages.unshift(message);
          group.fCAt = message.createdAt;
          group.messages.sort((m1, m2) => m1.createdAt - m2.createdAt);
        } else {
          group.messages.push(message);
          group.lCAt = message.createdAt;
          group.messages.sort((m1, m2) => m1.createdAt - m2.createdAt);
        }
      } else {
        group = {
          from_id: message.from_id,
          from_avatar: message.from_avatar,
          fCAt: message.createdAt,
          lCAt: message.createdAt,
          messages: [message],
        };
        this.grouped_messages.push(group);
      }
    },
    store_message(messages, append = false) {
      if (Array.isArray(messages)) {
        messages.map((m) => {
          this.group_message(m, append);
        });
      } else {
        this.group_message(messages, append);
      }
    },
    should_display_date(current_index) {
      let g1 = this.grouped_messages[current_index];
      let g2 = this.grouped_messages[current_index - 1];
      if (!g2) return true;

      if (g1.fCAt - g2.fCAt > 5 * 60) return true;

      return false;
    },
    on_new_message(data) {
      this.skip_ids.push(data.id);
      if(data.from_id == bpc_data.me && data.client_hash) {
        let i = sentHashes.findIndex(sm => sm.client_hash == data.client_hash);
        if(i > -1) {
          sentHashes.splice(i, 1);
        }
        return;
      }
      if (this.minimized) {
        this.jump = true;
        if (this.jump_timeout) clearTimeout(this.jump_timeout);
        this.jump_timeout = setTimeout(() => {
          this.jump = false;
        }, 500);
      }
      if (this.minimized || !this.buddy.focused) {
        this.$set(
          this.buddy,
          "unread_msg_count",
          this.buddy.unread_msg_count + 1
        );
      }
      if (!this.buddy.focused) {
        document.title = `${this.buddy.title} says (${this.buddy.unread_msg_count})`;
      }
      this.store_message(data, true);
      this.scroll_to_bottom();
    },
    formated_time(time_seconds) {
      let date = new Date(time_seconds * 1000);
      return date.toLocaleDateString("en-US", {
        year: "numeric",
        month: "short",
        day: "numeric",
        hour: "numeric",
        minute: "numeric",
      });
    },
    formated_message(message) {
      if(!message) return;
      return wrapEmoji(message);
    },
    get_message_class(message) {
      return message.from_id == bpc_data.me
        ? "message--self"
        : "message--other";
    },
    scroll_to_bottom() {
      this.$nextTick(() => {
        if (this.$refs.messageList) {
          //console.log(this.$refs.messageList, this.$refs.messageList.scrollHeight)
          this.$refs.messageList.scrollTop = this.$refs.messageList.scrollHeight;
        }
      });
    },
    is_user_online(user_id) {
      return this.online_users.indexOf(parseInt(user_id)) > -1;
    },
  },
  mounted() {
    this.ended = false;
    this.skip_ids = [];
    this.buddy.unread_msg_count = 0;
    this.waited_load_messages();

    this.buddy.bus.$on("new_message", (data) => {
      this.on_new_message(data);
    });

    this.$nextTick(() => {
      this.prevScrollPos = this.$refs.messageList.scrollTop;
      this.$refs.messageList.onscroll = (event) => {
        //console.log(event.target.scrollTop, this.prevScrollPos)
        if (event.target.scrollTop > this.prevScrollPos) {
          this.scrollingTop = false;
        } else if (event.target.scrollTop < this.prevScrollPos) {
          this.scrollingTop = true;
        }

        this.prevScrollPos = event.target.scrollTop;

        if (this.scrollingTop && event.target.scrollTop < 7) {
          //console.log('loading')
          this.load_messages(false);
        }
      };
    });
  },
};

function wrapEmoji(text) {
  text = text.replace(COLONS_REGEX, function(match, p1, p2) {
    const before = p1 || '';
    // We add "data-text='{emoji.native}'", don't replace it
    if (before.endsWith('alt="') || before.endsWith('data-text="')) {
      return match;
    }
    let emoji = emojiIndex.findEmoji(p2);
    if (!emoji) {
      return match;
    }
    return before + emojiToHtml(emoji);
  })
  return text.replace(EMOJI_REGEX, function(match, offset) {
    const before = text.substring(0, offset);
    if (before.endsWith('alt="') || before.endsWith('data-text="')) {
      // Emoji inside the replaced <img>
      return match;
    }
    // Find emoji object by native emoji.
    let emoji = emojiIndex.nativeEmoji(match);
    if (!emoji) {
      // Can't find unicode emoji in our index
      return match;
    }
    // See `emojiToHtml` function above.
    return emojiToHtml(emoji);
  })
}

function emojiToHtml(emoji) {
  let style = `background-position: ${emoji.getPosition()}; width:24px; height:24px;`;
  // The src="data:image..." is needed to prevent border around img tags.
  return `<img class="emoji-set-twitter emoji-type-image" data-text="${emoji.native}" alt="${
    emoji.colons
  }" src="data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7" class='emoji-text' style="${style}">`;
}
</script>


