<template>
  <div>
    <div class="chat-window__inputarea">
      <div class="chat-window__input">
        <div v-if="showPlaceholder" class="chat-window__input--placeholder">
          {{ placeholder }}
        </div>
        <div
          class="chat-window__input--field"
          contenteditable
          ref="editor"
          @input="on_input"
          @mouseup="onMouseUp"
          @keypress.enter="handleEnter"
          @keyup="track_caret_pos"
        ></div>
      </div>
      <div
        class="chat-window__input--emoji"
        @click="pickerShown = !pickerShown"
      >
        <span :style="smileStyle"></span>
      </div>
      <a
        href="#"
        class="chat-window__btn--enter"
        @click.prevent="on_click_enter"
        ><span><span class="dashicons dashicons-yes"></span></span
      ></a>
    </div>
    <div v-show="pickerShown" class="emoji-picker__container">
      <picker
        @select="on_emoji_selected"
        set="twitter"
        :data="emojiIndex"
        :i18n="i18n"
        :backgroundImageFn="emoji_img"
        :style="{ position: 'absolute', bottom: '60px', right: '-25px' }"
        :per-line="8"
        :emoji-size="24"
      />
    </div>
  </div>
</template>
<script>
import { Picker, EmojiIndex } from "emoji-mart-vue-fast";
import data from "emoji-mart-vue-fast/data/twitter.json";
import {decodeEntities} from "../helpers/html";

var emojiIndex = new EmojiIndex(data);

const sheet_url = `${bpc_data.image_dir_url}/sheet_twitter_64.png`;
const i18n = {
  search: decodeEntities(bpc_data.emoji.search),
  notfound: decodeEntities(bpc_data.emoji.notfound),
  categories: {
    search: decodeEntities(bpc_data.emoji.category_search),
    recent: decodeEntities(bpc_data.emoji.category_recent),
    smileys: decodeEntities(bpc_data.emoji.category_smileys),
    people: decodeEntities(bpc_data.emoji.category_people),
    nature: decodeEntities(bpc_data.emoji.category_nature),
    foods: decodeEntities(bpc_data.emoji.category_foods),
    activity: decodeEntities(bpc_data.emoji.category_activity),
    places: decodeEntities(bpc_data.emoji.category_places),
    objects: decodeEntities(bpc_data.emoji.category_objects),
    symbols: decodeEntities(bpc_data.emoji.category_symbols),
    flags: decodeEntities(bpc_data.emoji.category_flags),
  },
};
export default {
  name: "editable",
  components: {
    picker: Picker,
  },
  props: {
    value: {
      default: "",
      type: String,
    },
    placeholder: {
      default: "",
      type: String,
    },
    minimized: {
      default: true,
      type: Boolean,
    },
  },
  data() {
    return {
      tl: this.value ? this.value.length : 0,
      smileStyle: {
        display: "inline-block",
        width: "24px",
        height: "24px",
        backgroundImage: `url(${sheet_url})`,
        backgroundSize: "5700%",
        backgroundPosition: "53.57% 62.5%",
      },
      pickerShown: false,
      i18n: i18n,
      emojiIndex: emojiIndex,
    };
  },
  watch: {
    value(v) {
      if (!v) {
        this.$refs.editor.innerText = v;
        this.caretPos = 0;
      }
    },
    minimized(v) {
      this.pickerShown = false;
    },
  },
  computed: {
    showPlaceholder() {
      return this.value.length < 1;
    },
  },
  methods: {
    emoji_img() {
      return sheet_url;
    },
    onMouseUp() {
      this.track_caret_pos();
      this.pickerShown = false;
    },
    on_input(event) {
      this.$emit("input", event.target.innerHTML);
    },
    handleEnter(event) {
      if (event.key == "Enter" && !event.shiftKey) {
        event.preventDefault();
        this.$emit("submit", this.getFormatedValue());
      }
    },
    getFormatedValue() {
      if (!this.$refs.editor) return;
      let text = "";
      let childNodes = this.$refs.editor.childNodes;
      for (let i = 0; i < childNodes.length; i++) {
        switch (childNodes[i].nodeType) {
          case Node.ELEMENT_NODE:
            if (childNodes[i].nodeName == "IMG") {
              text += childNodes[i].alt;
            } else {
              text += childNodes[i].textContent;
            }
            break;
          case Node.TEXT_NODE:
            text += childNodes[i].textContent;
            break;
        }
      }
      return text;
    },
    on_click_enter() {
      let event = new KeyboardEvent("keyup", {
        key: "Enter",
        code: "Enter",
      });
      this.$emit("submit", this.getFormatedValue());
    },
    on_emoji_selected(emo) {
      let pos = this.caretPos || 0;
      let newS = this.$refs.editor.innerHTML;
      if (newS.lastIndexOf("\n\n") > -1) {
        newS = newS.substring(0, newS.length - 2);
      }
      let lastPart = newS.substring(pos);
      newS = newS.substring(0, pos) + emo.native + lastPart;

      this.$refs.editor.innerHTML = newS;
      this.$nextTick(() => {
        this.caretPos = this.$refs.editor.innerHTML.length - lastPart.length;
        this.$emit("input", this.$refs.editor.innerHTML);
      })
    },
    track_caret_pos(event) {
      this.caretPos = getCaretPosition(this.$refs.editor);
    },
  },
  mounted() {
    if (this.value) this.$refs.editor.innerHTML = this.value;
  },
};

// node_walk: walk the element tree, stop when func(node) returns false
function node_walk(node, func) {
  var result = func(node);
  for (
    node = node.firstChild;
    result !== false && node;
    node = node.nextSibling
  )
    result = node_walk(node, func);
  return result;
}

function getCaretPosition(elem) {
  elem.focus();
  let sel = document.getSelection();
  let pos = 0;
  let childNodes = elem.childNodes;
  if (sel.focusNode.nodeType == Node.ELEMENT_NODE) {
    for (let i = 0; i < childNodes.length; i++) {
      if (i == sel.focusOffset) {
        if (childNodes[i].nodeType == Node.TEXT_NODE) {
          pos += sel.focusOffset;
        }else{
          pos += sel.focusNode.outerHTML.length;
        }
        break;
      } else {
        if (childNodes[i].nodeType == Node.TEXT_NODE) {
          pos += childNodes[i].textContent.length;
        } else {
          pos += childNodes[i].outerHTML.length;
        }
      }
    }
  }else{
    for (let i = 0; i < childNodes.length; i++) {
      if (childNodes[i] == sel.focusNode) {
        pos += sel.focusOffset;
        break;
      } else {
        if (childNodes[i].nodeType == Node.TEXT_NODE) {
          pos += childNodes[i].textContent.length;
        } else {
          pos += childNodes[i].outerHTML.length;
        }
      }
    }
  }
  return pos;
}
</script>


